// React
import React, { useState } from 'react';
import { HashRouter, Routes, Route } from 'react-router-dom';
import { IntlProvider } from 'react-intl';
import { ReactLenis } from '@studio-freight/react-lenis'

// CSS
import './css/App.css';

// Component
import Header from './component/Header';
import MetaTheme from './component/MetaTheme';
import ScrollToTop from './component/ScrollToTop';

// Page
import Home from './page/Home';
import NotFound from './page/NotFound';
import Story from './page/work/Story';
import Offer from './page/work/Offer';
import Onboarding from './page/work/Onboarding';

// Lang
const messages = {
  en: require('./lang/en.json'),
  ko: require('./lang/ko.json'),
};

// App
const App = () => {

  // Language
  const [locale, setLocale] = useState('en');

  return (
    <ReactLenis root>
      <IntlProvider locale={locale} messages={messages[locale]}>
        <HashRouter basename={process.env.PUBLIC_URL}>

          {/* Common Components */}
          <MetaTheme />
          <ScrollToTop />
          <Header setLocale={setLocale} />

          {/* Page */}
          <Routes>

            {/* Home */}
            <Route exact path="/" element={<Home />} />

            {/* Work */}
            <Route path="/work/stories" element={<Story />} />
            <Route path="/work/offer-evaluation" element={<Offer />} />
            <Route path="/work/onboarding" element={<Onboarding />} />

            {/* 404 */}
            <Route path="*" element={<NotFound />} />
          </Routes>
        </HashRouter>
      </IntlProvider>
    </ReactLenis>
  );
}

export default App;