// React
import React, { useState, useEffect } from 'react';

// CSS
import './css/Timer.css';

// Timer
const Timer = () => {
  const [currentTime, setCurrentTime] = useState('');
  const [meridiem, setMeridiem] = useState('');

  // Function to display the current time in 12-hour format with AM or PM
  const updateLocalTime = () => {
    const now = new Date();
    let hours = now.getHours();
    const currentMeridiem = hours >= 12 ? ' PM' : ' AM';
    hours = hours % 12 || 12;
    let minutes = now.getMinutes();
    hours = hours < 10 ? '0' + hours : hours;
    minutes = minutes < 10 ? '0' + minutes : minutes;
    setCurrentTime(`${hours}:${minutes}`);
    setMeridiem(currentMeridiem);
  };

  // Update the time on initial load and every second
  useEffect(() => {
    updateLocalTime();

    const intervalId = setInterval(() => {
      updateLocalTime();
    }, 1000);

    // Clean up the interval on component unmount
    return () => clearInterval(intervalId);
  }, []); // Empty dependency array means this effect runs only once, similar to componentDidMount

  return (
    <div className="timer">
      <span className="dot"></span>
      <span>Seoul, {currentTime}{meridiem}</span>
    </div>
  );
}

export default Timer;