// React
import React, { useEffect, useRef } from 'react';

// CSS
import './css/Hr.css';

// Hr
const Hr = ({ style }) => {
  const hrRef = useRef(null);

  useEffect(() => {
    // Create a reference to the HR element
    const hr = hrRef.current;

    // Intersection Observer callback function
    const handleIntersection = (entries, observer) => {
      entries.forEach(entry => {

        // Check if the HR element is intersecting with the viewport
        if (entry.isIntersecting && entry.target.tagName === 'HR') {
          const hr = entry.target;

          // Add the '-loaded' class if not already added
          if (!hr.classList.contains('-loaded')) {
            hr.classList.add('-loaded');
          }
        } else {

          // Remove the '-loaded' class if the HR element is not intersecting with the viewport
          hr.classList.remove('-loaded');
        }
      });
    };

    // Create an Intersection Observer instance
    const observer = new IntersectionObserver(handleIntersection);

    // Observe the HR element
    observer.observe(hr);

    // Clean up function to disconnect the observer
    return () => {
      observer.disconnect();
    };
  }, []);

  return <hr ref={hrRef} style={style} />;
}

export default Hr;