// React
import React from 'react';

// Component
import Hr from './Hr';

// CSS
import './css/Section.css';

// Section
const Section = ({ config, line, children }) => {

  const hrStyle = {
    gridColumn: '1 / -1',
    marginBottom: '4rem'
  };

  return (
    <section className={config}>
      {line && <Hr style={hrStyle} />}
      {children}
    </section>
  );
}

export default Section;