// React
import React, { useEffect } from 'react';

// Component
import Cell from '../../component/Cell';
import Footer from '../../component/Footer';
import HeroSection from '../../component/HeroSection';
import Img from '../../component/Img';
import LinkText from '../../component/LinkText';
import Numeric from '../../component/Numeric';
import Section from '../../component/Section';
import Text from '../../component/Text';
import Title from '../../component/Title';
import Video from '../../component/Video';

// Source
import img01 from './source/sty-img-01.jpg';
import img02 from './source/sty-img-02.jpg';
import img03 from './source/sty-img-03.jpg';
import img04 from './source/sty-img-04.jpg';
import img05 from './source/sty-img-05.jpg';
import img06 from './source/sty-img-06.jpg';
import vid01 from './source/sty-vid-01.mp4';

import nav02 from '../source/img-02.jpg';
import nav03 from '../source/img-03.jpg';

// Story
const Story = () => {

  useEffect(() => {

    // Additional page id
    const body = document.querySelector('body');
    if (body) {
      body.setAttribute('class', '-work');
    }

    // Additional page title
    const originalTitle = document.title;
    const additionalTitle = 'Stories  ';
    document.title = additionalTitle + originalTitle;

    return () => {
      document.title = originalTitle;
    };
  }, []);

  return (
    <>
      {/* Hero */}
      <HeroSection content="Stories" />

      {/* Introduction */}
      <Section line={true}>
        <Cell config="-head -small">
          <Text config="-label">
            Blind is preparing to launch an image-based Story feature as it plans a leap towards being a community service where a more diverse range of content is produced. The Blind Story project aims to enhance content diversification and intends to expand new advertising inventory, improving both engagement rates and monetization.
          </Text>
        </Cell>
        <Cell config="-grid -small">
          <Text config="-end">Teamblind</Text>
          <Text config="-end">2023  24</Text>
        </Cell>
      </Section>

      {/* Overview */}
      <Section>
        <Cell config="-fill">
          <Img title="" src={img01} />
        </Cell>
      </Section>

      {/* Goal */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Objective" />
        </Cell>
        <Cell config="-content">
          <Text>
            The Story feature aims for a rapid release with minimal functionality to quickly grasp and evolve user patterns. The project was constrained in duration, requiring swift planning and design definition as the foundation for a rapid development process to commence.
          </Text>
          <Cell config="-grid">
            <Numeric config="-unit" caption="Project Duration" value="100" unit="days" />
          </Cell>
        </Cell>
      </Section>

      {/* Case study */}
      <Section>
        <Cell config="-gallery">
          <Cell>
            <Img config="-screen" title="" src={img02} />
            <Text config="-sub -dim">In-Feed AD</Text>
          </Cell>
          <Cell>
            <Img config="-screen" title="" src={img03} />
            <Text config="-sub -dim">FAB: Full Screen AD</Text>
          </Cell>
        </Cell>
      </Section>

      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Case study" />
        </Cell>
        <Cell>
          <Text>
            A swift decision was made to insert the Story feature into the existing post feed for quick validation after launch. To ensure high accessibility for Stories, we tested the UI for the entry point of Stories based on the most prominent form of various content types, the existing advertising inventory UI. The existing advertising feed consists of Banner, In-Feed, and FAB formats, and it was necessary to ensure that Stories are easily accessible by users in a differentiated UI where they hold the same priority level.
          </Text>
        </Cell>
      </Section>

      <Section>
        <Cell config="-gallery">
          <Cell>
            <Img title="" src={img04} />
            <Text config="-sub -dim">A: In-Feed</Text>
          </Cell>
          <Cell>
            <Img title="" src={img05} />
            <Text config="-sub -dim">B: Carousel</Text>
          </Cell>
        </Cell>
      </Section>

      <Section>
        <Cell config="-null" />
        <Cell>
          <Text>
            Stories are provided in a preview form within the feed, offering users an enhanced experience of consuming more story content upon entry. It was crucial that the story previews did not disrupt the feed browsing experience. For rapid development, the existing advertising UI was repurposed to offer a single preview In-Feed type, and although it required additional development effort, a more scalable Carousel format was tested.
          </Text>
        </Cell>
      </Section>

      {/* Result */}
      <Section>
        <Cell config="-fill">
          <Img config="-screen" title="" src={img06} />
        </Cell>
      </Section>

      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Result" />
        </Cell>
        <Cell>
          <Text>
            The In-Feed UI allowed for rapid development but was suited for single content forms and had the disadvantage of disrupting the post browsing experience. Therefore, the Story feature adopted the Carousel UI to enable users to easily preview various contents without interrupting their content consumption experience. A text button was added to encourage users who found the previews appealing to enter the Stories.
          </Text>
        </Cell>
      </Section>

      {/* Achievements */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Achievements" />
        </Cell>
        <Cell config="-content">
          <Cell>
            <Text>
              Upon segmenting user groups to assess the contribution of Stories to growth, it was found that users who experienced Stories exhibited positive data changes, significantly impacting overall engagement rates. After the launch of Stories, the average daily total sessions increased by 10%, and the dwell time increased by 11%. For users who experienced Stories (approximately 40% of DAU), the average number of sessions per user per day increased by 8%, and the dwell time increased by 12%, contributing to the overall increase in metrics. This translates to an average increase in session length of about 80 seconds per day.
            </Text>
            <LinkText config="-dim" type="a" content="Download /App Store " hover="Download /App Store " href="https://apps.apple.com/us/app/blind-professional-community/id737534965" />
          </Cell>
          <Cell config="-grid">
            <Numeric config="-unit" caption="Average Sessions" value="10" unit="%" />
            <Numeric config="-unit" caption="Average Session Length" value="80" unit="sec." />
          </Cell>
          <Cell>
            <Text>
              For users who engaged with Stories, the average daily content views per user increased by 42%, and post views by 8%, resulting in an overall increase in average daily content views per user by 21%, with post views increasing by 5%. The average daily content creation per user rose by 8%, post creations by 4%, and the total number of comments made increased by about 5%. It was observed that the number of posts created by the group of users who experienced Stories showed an increasing trend after the launch of Stories, indicating a positive impact on the number of posts created. Additionally, with the launch of Stories, the total number of likes per user increased by 75%, and the number of likes per post per user also increased by 31%. Users who had experienced Stories at least once showed a significant increasing trend in the number of likes on posts compared to before.
            </Text>
            <Text config="-dim">Dec. 2024  Feb. 2024, Data Team</Text>
          </Cell>
          <Cell config="-grid">
            <Numeric config="-unit" caption="Content Views" value="21" unit="%" />
            <Numeric config="-unit" caption="Post Views" value="5" unit="%" />
          </Cell>
        </Cell>
      </Section>

      {/* Next */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="What's The Next?" />
        </Cell>
        <Cell>
          <Text>
            Among the total DAU of Blind, 40% are users who have experienced Stories, indicating the need to identify improvement points to attract users without Story experience to the feature. Additionally, beyond likes, further feature development is necessary to facilitate more interactions within Stories.
          </Text>
        </Cell>
      </Section>

      {/* Live product demo */}
      <Section>
        <Cell config="-fill">
          <Video src={vid01} />
        </Cell>
      </Section>

      {/* Lesson learned */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Lesson learned" />
        </Cell>
        <Cell config="-content">
          <Text>
            Unlike previous projects, which were judged based on the achievement of set goals, this Story project was conducted in a test format that allowed for quick identification of user patterns and enabled the identification of future improvement points based on data. It was learned that, in certain cases, the ability to fully leverage advantages can be developed when rapid execution is prioritized over conservative goal setting in project planning.
          </Text>
          <Text>
            Blind Stories showcased a pattern of engagement based on popular content posted by users, with many pieces of content related to showcasing their appeal. Consequently, this led to an increase in the sales of private messages on Blind, as users purchased messages to contact creators of attractive content.
          </Text>
        </Cell>
      </Section>

      {/* Other projects */}
      <Section line={true}>
        <Cell config="-head -fill">
          <Text config="-sub" bullet={true}>Work</Text>
          <Title config="-title" content="Other projects" />
        </Cell>
        <Cell config="-gallery">
          <Cell to="/work/offer-evaluation">
            <Img title="Find a new opportunity" src={nav02} />
            <LinkText index="02" content="Offer Evaluation" hover="View More " />
          </Cell>
          <Cell to="/work/onboarding">
            <Img title="Be heared, Stay anonymous" src={nav03} />
            <LinkText index="03" content="Onboarding 2.0" hover="View More " />
          </Cell>
        </Cell>
      </Section>

      {/* Footer */}
      <Footer />
    </>
  );
}

export default Story;