// React
import React, { useEffect } from 'react';

// Component
import Cell from '../../component/Cell';
import Footer from '../../component/Footer';
import HeroSection from '../../component/HeroSection';
import Img from '../../component/Img';
import LinkText from '../../component/LinkText';
import Numeric from '../../component/Numeric';
import Section from '../../component/Section';
import Text from '../../component/Text';
import Title from '../../component/Title';
import Video from '../../component/Video';

// Source
import img01 from './source/onb-img-01.jpg';
import img02 from './source/onb-img-02.jpg';
import img03 from './source/onb-img-03.jpg';
import img04 from './source/onb-img-04.jpg';
import img05 from './source/onb-img-05.jpg';
import img06 from './source/onb-img-06.jpg';
import vid01 from './source/onb-vid-01.mp4';

import nav01 from '../source/img-01.jpg';
import nav02 from '../source/img-02.jpg';

// Onboarding
const Onboarding = () => {

  useEffect(() => {

    // Additional page id
    const body = document.querySelector('body');
    if (body) {
      body.setAttribute('class', '-work');
    }

    // Additional page title
    const originalTitle = document.title;
    const additionalTitle = 'Onboarding 2.0  ';
    document.title = additionalTitle + originalTitle;

    return () => {
      document.title = originalTitle;
    };
  }, []);

  return (
    <>
      {/* Hero */}
      <HeroSection content="Onboarding 2.0" />

      {/* Introduction */}
      <Section line={true}>
        <Cell config="-head -small">
          <Text config="-label">
            The dropout rate during the Blind's sign-up process was approximately 40%. In response, we revamped the onboarding process to increase the influx of users to Blind. Although it is a community that can be used based on company email verification, we also made changes to allow users with personal emails to enjoy Blind content, albeit in a limited capacity, in order to lower the barrier to entry.
          </Text>
        </Cell>
        <Cell config="-grid -small">
          <Text config="-end">Teamblind</Text>
          <Text config="-end">2023</Text>
        </Cell>
      </Section>

      {/* Overview */}
      <Section>
        <Cell config="-fill">
          <Img title="" src={img01} />
        </Cell>
      </Section>

      {/* Goal */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Objective" />
        </Cell>
        <Cell>
          <Text>
            In the original Blind onboarding process, we focused on making the transition to the company email verification process more intuitive and improving the accessibility for users with personal emails. This was done with the primary goal of increasing user influx to Blind.
          </Text>
        </Cell>
      </Section>

      {/* Case study */}
      <Section>
        <Cell config="-gallery">
          <Cell>
            <Img config="-screen" title="" src={img02} />
            <Text config="-sub -dim">As-is: Hero Screen</Text>
          </Cell>
          <Cell>
            <Img config="-screen" title="" src={img03} />
            <Text config="-sub -dim">As-is: Swipe</Text>
          </Cell>
        </Cell>
      </Section>

      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Case study" />
        </Cell>
        <Cell>
          <Text>
            The existing onboarding process lacked clear prioritization of information regarding the description of the Blind service, images, and entry countries. Additionally, the page for the service's catchphrase was malfunctioning to the extent that it could not extract proper data.
          </Text>
        </Cell>
      </Section>

      {/* What we changed */}
      <Section>
        <Cell config="-gallery">
          <Cell>
            <Img config="-screen" title="" src={img04} />
            <Text config="-sub -dim">To-be: Hero Screen</Text>
          </Cell>
          <Cell>
            <Img config="-screen" title="" src={img05} />
            <Text config="-sub -dim">Permission Comparison</Text>
          </Cell>
        </Cell>
      </Section>

      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="What we changed" />
        </Cell>
        <Cell>
          <Text>
            The entry screen for the new Blind onboarding process was restructured based on the priority of each function. Additionally, by incorporating the signature pattern of the Blind logo along with a phrase that most succinctly captures the concept of the Blind service, we further strengthened the brand identity design.
          </Text>
        </Cell>
      </Section>

      <Section>
        <Cell config="-fill">
          <Img config="-square" title="" src={img06} />
        </Cell>
      </Section>

      <Section>
        <Cell config="-null" />
        <Cell>
          <Text>
            At every step where users opt to use personal emails, a Bottom Sheet comparing the permissions with those of a company email is displayed, recommending the most advantageous option from the creator's perspective. However, to increase user influx, we improved the visibility of the personal email sign-up option. Additionally, we added a verification phrase about Blind's anonymity logic to alleviate users' anxiety, utilizing it as a reassuring element.
          </Text>
        </Cell>
      </Section>

      {/* Achievements */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Achievements" />
        </Cell>
        <Cell config="-content">
          <Cell>
            <Text>
              As a result of the improved onboarding process, company email registrations increased by 11%, and personal email registrations surged by 40%. It was evident that the dropout rate significantly decreased to 16% from its previous level, and there was a substantial rise in the demand for personal email registrations.
            </Text>
            <LinkText config="-dim" type="a" content="Download /App Store " hover="Download /App Store " href="https://apps.apple.com/us/app/blind-professional-community/id737534965" />
          </Cell>
          <Cell config="-grid">
            <Numeric config="-unit" caption="Work Email" value="11" unit="%" />
            <Numeric config="-unit" caption="Personal Email" value="40" unit="%" />
          </Cell>
        </Cell>
      </Section>

      {/* Live product demo */}
      <Section>
        <Cell config="-fill">
          <Video src={vid01} />
        </Cell>
      </Section>

      {/* Lesson learned */}
      <Section>
        <Cell config="-head -small">
          <Title config="-headline" content="Lesson learned" />
        </Cell>
        <Cell>
          <Text>
            The increase in personal email registrations exceeded expectations, serving as a pivotal moment to recognize Blind's potential to evolve from a community based on company email verification into a more inclusive anonymous community capable of accommodating a broader user base.
          </Text>
        </Cell>
      </Section>

      {/* Other projects */}
      <Section line={true}>
        <Cell config="-head -fill">
          <Text config="-sub" bullet={true}>Work</Text>
          <Title config="-title" content="Other projects" />
        </Cell>
        <Cell config="-gallery">
          <Cell to="/work/stories">
            <Img title="Anonymous stories" src={nav01} />
            <LinkText index="01" content="Stories" hover="View More " />
          </Cell>
          <Cell to="/work/offer-evaluation">
            <Img title="Find a new opportunity" src={nav02} />
            <LinkText index="02" content="Offer Evaluation" hover="View More " />
          </Cell>
        </Cell>
      </Section>

      {/* Footer */}
      <Footer />
    </>
  );
}

export default Onboarding;