// React
import React, { useState, useEffect, useRef } from 'react';

// Component
import Text from './Text';

// CSS
import './css/Numeric.css';

// Numeric
const Numeric = ({ config, caption, value, unit }) => {

  // State variables to manage the current number and animation status.
  const [currentValue, setCurrentValue] = useState(0);
  const [isIntersecting, setIsIntersecting] = useState(false);

  // Ref for Intersection Observer.
  const numericRef = useRef(null);

  useEffect(() => {

    // Setting up Intersection Observer.
    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {

        // Start or stop animation when the Numeric element enters or exits the viewport.
        setIsIntersecting(entry.isIntersecting);
      });
    });

    // Observe the Numeric element.
    if (numericRef.current) {
      observer.observe(numericRef.current);
    }

    // Clean up Intersection Observer when the component unmounts.
    return () => {
      observer.disconnect();
    };
  }, []);

  useEffect(() => {

    // Start animation only when the Numeric element is in the viewport.
    if (isIntersecting) {

      const duration = 600; // Duration of the animation (milliseconds)
      const steps = duration / value; // Time steps to raise the number to the value
      const interval = setInterval(() => {
        if (currentValue < value) {
          setCurrentValue((prevValue) => {

            // Increment currentValue until it reaches the value.
            const newValue = prevValue + 1;

            // Ensure newValue does not exceed the value.
            return newValue > value ? value : newValue;
          });
        }
      }, steps);

      // Clear the interval when the animation stops.
      return () => clearInterval(interval);
    } else {

      // Reset the number when it exits the viewport.
      setCurrentValue(0);
    }
  }, [isIntersecting, currentValue, value]);

  // Add a comma every thousand for the displayed number.
  const formattedValue = currentValue.toLocaleString();

  // Add the configured class if provided.
  const classes = config ? `numeric ${config}` : 'numeric';

  return (
    <div ref={numericRef} className={classes}>
      <div>
        <span>{formattedValue}</span>
        <span>{unit}</span>
      </div>
      {caption && <Text>{caption}</Text>}
    </div>
  );
}

export default Numeric;