// React
import React, { useState } from 'react';

// CSS
import './css/LanguageSelector.css';

// LanguageSelector
const LanguageSelector = ({ setLocale }) => {
  const [currentLocale, setCurrentLocale] = useState('en');
  const [toggleOffset, setToggleOffset] = useState(0);

  const handleLocaleChange = (newLocale) => {
    if (newLocale !== currentLocale) {
      setToggleOffset(toggleOffset === 0 ? -100 : 0);
      setCurrentLocale(newLocale);
      setLocale(newLocale);
    }
  };

  return (
    <div className="language">
      <button onClick={() => handleLocaleChange('ko')} className={currentLocale === 'ko' ? '-hidden' : ''} style={{ transform: `translate3d(0, ${toggleOffset}%, 0)` }}>
        kr
      </button>
      <button onClick={() => handleLocaleChange('en')} className={currentLocale === 'en' ? '-hidden' : ''} style={{ transform: `translate3d(0, ${toggleOffset}%, 0)` }}>
        en
      </button>
    </div>
  );
};

export default LanguageSelector;