// React
import React, { useEffect, useState } from 'react';

// CSS
import './css/Header.css';

// Component
import LanguageSelector from './LanguageSelector';
import LinkText from './LinkText';

// Header Component
const Header = ({ setLocale }) => {

  // Add translate3D state with an initial value of 'Translate3D(-100%)'.
  const [translate3D, setTranslate3D] = useState('translate3d(0, -100%, 0)');

  useEffect(() => {

    // Set translate3D to 'Translate3D(0)' when the component mounts.
    setTranslate3D('translate3d(0, 0, 0)');

    let lastScrollY = window.scrollY;

    const updatePosition = () => {

      // If the scroll value from the top of the viewport is less than 200,
      // always display the header with Translate3D at 0.
      if (window.scrollY < 200) {
        setTranslate3D('translate3d(0, 0, 0)');
      } else {

        // Update translate3D value based on scroll direction.
        if (window.scrollY > lastScrollY) {

          // If scrolling down,
          setTranslate3D('translate3d(0, -100%, 0)');
        } else {

          // If scrolling up,
          setTranslate3D('translate3d(0, 0, 0)');
        }
      }

      lastScrollY = window.scrollY;
    };

    // Register a scroll event listener.
    window.addEventListener('scroll', updatePosition);

    // Remove the event listener when the component unmounts.
    return () => {
      window.removeEventListener('scroll', updatePosition);
    };
  }, []);

  return (
    <header style={{ transform: translate3D }}>
      <nav>
        <LinkText config="-sub" type="a" content="Seung-Hyun" hover="Seung-Hyun" to="/" />
        <div className="menu">
          <LinkText config="-sub -end" type="a" content="Contact" hover="Contact" href="mailto:hello@seung-hyun.me?subject=Hi Seung-hyun!" />
          <LanguageSelector setLocale={setLocale} />
        </div>
      </nav>
    </header>
  );
}

export default Header;